// import GoogleMapsApiLoader from "google-maps-api-loader";
// import geofire from 'geofire';
import firebase from "firebase";
import coreDataService from "../coreDataService";

// let CONSTVAL = "";
const geofire = require("geofire");
export default {
  data() {
    return {
