      loadingNot: false,
      coreDirectionalDataRoutes: null,
      rinfoModal: false,
      addressPickup: "",
      addressDst: "",
      address: "",
      error: "",
      clearDst: "",
      clearSt: "",
      spinner: false,
      apiKey: "AIzaSyBff3nfeR8sNbSrj0ML9U8KyJoJrk0lFts",
      google: null,
      pickAddr: {},
      dstAddr: {},
      totalFare: { ride: 0, bajaj: 0 },
      maxDriversAccepted: 5,
      serverToken:
        "key=AAAA1DpMn3o:APA91bF7hz5O6TdcgLNw5R1x_9b2VekDRWzfIRMXo93FY0qyJ0EtFXIM7DDcFU6xlUbthBOm1AkI2E4fE8uNJ1eNBMA6LC_zu2t2Z2BVi2b2on4FAL1Cv1Sa3w2nPfyyKqEbMTYYArH4",
      allAvailableDrivers: [],
      allAvailableCoreDrivers: [],
      riderData: {
        fullName: "",
        phone: null,
        type: "bajaj",
        types: ["bajaj", "taxi"],
      },
      allAvailableDriversLoc: [],
      tokenMain: "",
      xL: "",
      allLocs: [],
    };
  },
  async mounted() {
    this.google = window.google;
    // bole medhanialem centeral address
    this.plotAllDrivers(8.995834799999999, 38.7900826);
    // this.locatorButtonPressed().then((locs) => {
    //   locs.forEach((lc) => {
    //     new this.google.maps.Marker({
    //       position: new this.google.maps.LatLng(lc.loc[0], lc.loc[1]),
    //       map: map,
    //       title: "Click Me ",
    //     });
    //   });
    // });
    // this.allLocs.forEach((lc) => {
    //   const nloc = new this.google.maps.LatLng(lc.loc[0], lc.loc[1]);
    // });

    // recognize and mount the of the pickup address
    var autocompletePick = new this.google.maps.places.Autocomplete(
      this.$refs["autocompletePick"],
      {
        bounds: new this.google.maps.LatLngBounds(
          new this.google.maps.LatLng(45.4215296, -75.6971931)
        ),
      }
    );
    const directionService = new this.google.maps.DirectionsService();
    const directionsRenderer = new this.google.maps.DirectionsRenderer();
    autocompletePick.addListener("place_changed", () => {
      var pickPlace = autocompletePick.getPlace();
      this.clearSt = pickPlace.name;
      this.showLocationOnTheMap(
        pickPlace.geometry.location.lat(),
        pickPlace.geometry.location.lng()
      );
      this.pickAddr = {
        lat: pickPlace.geometry.location.lat(),
        lng: pickPlace.geometry.location.lng(),
      };
      var map = new this.google.maps.Map(this.$refs["map"], {
        zoom: 15,
        center: new this.google.maps.LatLng(
          this.pickAddr.lat,
          this.pickAddr.lng
        ),
        mapTypeId: this.google.maps.MapTypeId.ROADMAP,
      });
      directionsRenderer.setMap(map);
      var icon = {
        url: require("@/assets/taxi.png"), // url
        scaledSize: new this.google.maps.Size(60, 60), // scaled size
        // origin: new this.google.maps.Point(0, 0), // origin
        // anchor: new this.google.maps.Point(0, 0), // anchor
      };

      if (this.dstAddr.lat && this.dstAddr.lng) {
        this.calculateAndDisplayRoute(directionService, directionsRenderer);
        this.traceDriversLocation();
        this.locs.forEach((lc) => {
          var marker = new this.google.maps.Marker({
            position: new this.google.maps.LatLng(lc.loc[0], lc.loc[1]),
            map: map,
            icon: icon,
            title: lc.driver.name,
            label: {
              color: "white",
              fontWeight: "bold",
              fontSize: "16px",
            },
          });
          var infowindow = new this.google.maps.InfoWindow({
          content: `<h3>${lc.driver.name}</h3><h4>${lc.driver.phone}</h4>`,
          });
          marker.addListener("click", function () {
            infowindow.open(map, marker);
          });
        });
      } else {
        this.plotAllDrivers(this.pickAddr.lat, this.pickAddr.lng);
      }
    });
    // recognize and mount the of the pickup address
    var autocompleteDst = new this.google.maps.places.Autocomplete(
      this.$refs["autocompleteDst"],
      {
        bounds: new this.google.maps.LatLngBounds(
          new this.google.maps.LatLng(45.4215296, -75.6971931)
        ),
      }
    );
    autocompleteDst.addListener("place_changed", () => {
      var place = autocompleteDst.getPlace();
      this.clearDst = place.name;
      this.dstAddr = {
        lat: place.geometry.location.lat(),
        lng: place.geometry.location.lng(),
      };
      var map = new this.google.maps.Map(this.$refs["map"], {
        zoom: 15,
        center: new this.google.maps.LatLng(
          this.pickAddr.lat,
          this.pickAddr.lng
        ),
        mapTypeId: this.google.maps.MapTypeId.ROADMAP,
      });
      directionsRenderer.setMap(map);
      // add some riders man
      // let geoFireApp = new geofire.GeoFire(
      //   firebase.database().ref().child("availableDrivers")
      // );
      // geoFireApp.set("XJ0yfQ7qLZOrzDzbFmntx34SBgL2", [
      //   this.dstAddr.lat,
      //   this.dstAddr.lng,
      // ]);
      // setting drivers at this location for testing
      // let ids = [
      //   "DLZCDJ33uwPMWuVkq0rc27aIamE3",
      //   "c3YAxqbFIehVytLh0pYaZrjs1cl2",
      //   "rkfysQqPU6NUwqD76Bd2wjFUngS2",
      // ];
      // ids.forEach((x) => {
      //   geoFireApp.set(x, [this.pickAddr.lat, this.pickAddr.lng]);
      // });
      var icon = {
        url: require("@/assets/taxi.png"), // url
        scaledSize: new this.google.maps.Size(60, 60), // scaled size
        // origin: new this.google.maps.Point(0, 0), // origin
        // anchor: new this.google.maps.Point(0, 0), // anchor
      };
      if (this.pickAddr.lat && this.pickAddr.lng) {
        this.calculateAndDisplayRoute(directionService, directionsRenderer);
        this.traceDriversLocation();
        this.locs.forEach((lc) => {
          var marker = new this.google.maps.Marker({
            position: new this.google.maps.LatLng(lc.loc[0], lc.loc[1]),
            map: map,
            icon: icon,
            title: lc.driver.name,
            label: {
              text: lc.driver.name,
              color: "white",
              fontWeight: "bold",
              fontSize: "16px",
            },
          });
          var infowindow = new this.google.maps.InfoWindow({
          content: `<h3>${lc.driver.name}</h3><h4>${lc.driver.phone}</h4>`,
          });
          marker.addListener("click", function () {
            infowindow.open(map, marker);
          });
        }); // this.plotAllDrivers(this.pickAddr.lat, this.pickAddr.lng);
      } else {
        this.plotAllDrivers(this.pickAddr.lat, this.pickAddr.lng);
      }
    });
    // this.locatorButtonPressed();
  },
  methods: {
    proceed() {
      if (this.validUsrData) {
        this.initRiReq = true;
      } else {
        this.showMessage("error", 1200, "Complete rider information first !!!");
      }
    },
    saveRiderData() {
      if (this.validUsrData) {
        this.rinfoModal = false;
      } else {
        this.showMessage("error", 1000, "Incomplete rider information !!!");
      }
    },
    generate_token(length) {
      var a = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890".split(
        ""
      );
      var b = [];
      for (var i = 0; i < length; i++) {
        var j = (Math.random() * (a.length - 1)).toFixed(0);
        b[i] = a[j];
      }
      return b.join("");
    },
    notifyDriver() {
      if (this.allAvailableDrivers.length > 0) {
        let driver = this.allAvailableDrivers[0];
        coreDataService
          .driversData()
          .child(driver.did)
          .child("car_details")
          .child("type")
          .once(
            "value",
            (res) => {
              const currDriverIdx = this.allAvailableDrivers.indexOf(driver);
              if (res.exists()) {
                const typeNode = res.val();
                if (this.riderData.type == typeNode.toLowerCase()) {
                  // remove rider from the list but wait for 10 sec first
                  setTimeout(() => {
                    this.sendNotificationToDriver(this.xL, driver);
                    this.allAvailableDrivers.splice(currDriverIdx, 1);
                    this.notifyDriver();
                  }, 10000);
                } else {
                  this.showMessage(
                    "error",
                    800,
                    `${this.riderData.type} Driver not found !!!`
                  );
                }
              } else {
                this.showMessage("error", 1200, "NO Drivers found at all");
                this.loadingNot = false;
              }
            },
            (err) => {
              console.error(err);
            }
          );
      } else {
        this.showMessage(
          "error",
          800,
          `${this.riderData.type} Drivers not found !!!`
        );
      }
    },
    saveRideRequest() {
      const requestRef = coreDataService.rideReq().push();
      const pickUpLocMap = {
        latitude: this.pickAddr.lat,
        logitude: this.pickAddr.lng,
      };
      const dropOffLocMap = {
        latitude: this.dstAddr.lat,
        logitude: this.dstAddr.lng,
      };
      console.error(this.riderData);
      const rideInfoMap = {
        driver_id: "waiting",
        payment_method: "cash",
        pickup: pickUpLocMap,
        dropoff: dropOffLocMap,
        created_at: new Date().toString(),
        rider_name: this.riderData.fullName,
        rider_phone: this.riderData.phone,
        pickup_address: this.clearSt,
        dropoff_address: this.clearDst,
        // select ride type from the user
        ride_type: this.riderData.type,
        // set the accepted status
        fromAdmin: true,
      };
      requestRef.set(rideInfoMap);
      requestRef
        .once("value", (res) => {
          this.xL = res.key;
        })
        .catch((e) => {
          console.error(e);
        });
    },
    getFireTrace() {
      if (this.allAvailableDrivers.length == 0) {
        this.showMessage("error", 1200, "No drivers found");
        this.noDrivers = true;
        this.loadingNot = false;
      } else {
        this.notifyDriver();
      }
    },
    async sendNotificationToDriver(ride_request_id, driver) {
      console.log(driver, "AA");
      // recieve the ride request Id and send push notification to that device
      // let mx = this;
      const header = {
        "Content-Type": "application/json",
        Authorization: this.serverToken,
      };
      const notificationBody = {
        body: `DropOff Address, ${this.clearDst}`,
        title: "New Ride Request",
      };
      const coreBlock = {
        click_action: "FLUTTER_NOTIFICATION_CLICK",
        id: "1",
        status: "done",
        // ride_request_id: "accepted",
        ride_request_id: ride_request_id,
      };
      this.loadingNot = true;
      coreDataService
        .driversData()
        .child(driver.did)
        .child("token")
        .once("value", () => {})
        .then((x) => {
          const sendNotificationBlock = {
            notification: notificationBody,
            data: coreBlock,
            priority: "high",
            to: x.val(),
          };
          axios
            .post(
              "https://fcm.googleapis.com/fcm/send",
              sendNotificationBlock,
              {
                headers: header,
              }
            )
            .then((res) => {
              this.loadingNot = false;
              this.initRiReq = false;
              console.log(res);
              var conf = {
                position: "top-right",
                timeout: 1000,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                draggablePercent: 0.6,
                hideProgressBar: true,
                showCloseButtonOnHover: false,
                closeButton: "button",
                icon: true,
              };
              if (res.data.success == 1) {
                console.log(this.$toast);
                this.$toast.success(
                  "Push notification successfully sent to driver !!!!!",
                  conf
                );
                this.resetEverything();
              }
            })
            .catch((e) => {
              this.resetEverything();
              this.loadingNot = false;
              console.error("Error sending notification =>", e);
            });
        });

      // final payload to be sent
    },
    resetEverything() {
      this.$refs.form.reset();
    },
    getAllAvailableDrivers() {
      let geoFireApp = new geofire.GeoFire(
        firebase.database().ref().child("availableDrivers")
      );
      let lim = 1;
      let allDr = [];
      geoFireApp
        .query({
          center: [this.pickAddr.lat, this.pickAddr.lng],
          // center: [40.607765, -73.758949],
          radius: 10,
        })
        .on("key_entered", (key, location, distance) => {
          // get the driver at this point and build the whole model
          lim++;
          if (lim <= this.maxDriversAccepted) {
            allDr.push({
              did: key,
              location: location,
              distance: distance,
            });
          }
          console.log(
            key +
              " entered query at " +
              location +
              " (" +
              distance +
              " km from center)"
          );
        });

      // alert(allDr.slice(0, this.maxDriversAccepted).length);
      // this.allAvailableDrivers = allDr;
      // only get 5 of all the available drivers
      // alert(allDr.slice(0, this.maxDriversAccepted).length)
      this.allAvailableDrivers = allDr;
      // alert(this.allAvailableDrivers.length);
    },
    calculateFare(directionDetails) {
      const timeTravelFare = (directionDetails.durationValue / 60) * 0.8;
      const distanceTraveled = (directionDetails.distanceValue / 1000) * 13.0;
      // double initialFare = 20;
      const totalFareAmount = timeTravelFare + distanceTraveled;
      return totalFareAmount;
    },
    calculateFareBajaj(directionDetails) {
      const timeTravelFare = (directionDetails.durationValue / 60) * 0.5;
      const distanceTraveled = (directionDetails.distanceValue / 1000) * 10.0;
      // double initialFare = 20;
      const totalFareAmount = timeTravelFare + distanceTraveled;
      return totalFareAmount;
    },
    manageDirectionalDetails() {
      let cr = this.coreDirectionalDataRoutes;
      let coreDetails = {
        encodedPoints: cr.overview_polyline,
        distanceText: cr.legs[0].distance.text,
        distanceValue: cr.legs[0].distance.value,
        durationText: cr.legs[0].duration.text,
        durationValue: cr.legs[0].duration.value,
      };
      this.totalFare = {
        ride: this.calculateFare(coreDetails),
        bajaj: this.calculateFareBajaj(coreDetails),
      };
    },
    calculateAndDisplayRoute(directionsService, directionsRenderer) {
      directionsService.route(
        {
          origin: this.clearSt,
          destination: this.clearDst,
          travelMode: this.google.maps.TravelMode.DRIVING,
        },
        (response, status) => {
          if (status === "OK") {
            directionsRenderer.setDirections(response);
            this.coreDirectionalDataRoutes = response.routes[0];
            this.manageDirectionalDetails();
            this.getAllAvailableDrivers();
            this.readyForReq = true;
          } else {
            this.showMessage(
              "error",
              1200,
              "Directions request failed due to " + status
            );
          }
        }
      );
      // search for all the available drivers after the calculation
    },
    createRideRequestDial() {
      this.loadingNot = true;
      this.createRideRequest();
    },
    createRideRequest() {
      if (this.pickAddr.lat && this.pickAddr.lng) {
        if (this.dstAddr.lat && this.dstAddr.lng) {
          // save the ride request first
          this.saveRideRequest();
          this.getFireTrace();
        }
      } else {
        console.error("inputs are not clean");
      }
    },
    async traceDriversLocation() {
      this.spinner = true;
      await coreDataService.availableDrivers().once("value", (data) => {
        for (let [key, value] of Object.entries(data.val())) {
          coreDataService
            .driversData()
            .child(key)
            .once("value")
            .then((res) => {
              this.locs.push({ loc: value.l, key: key, driver: res.val() });
            });
        }
      });
    },
    getAddressFrom(lat, long) {
      axios
        .get(
          "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
            lat +
            "," +
            long +
            "&key=AIzaSyBff3nfeR8sNbSrj0ML9U8KyJoJrk0lFts"
        )
        .then((response) => {
          if (response.data.error_message) {
            this.error = response.data.error_message;
            console.log(response.data.error_message);
          } else {
            // this.addressPickup = response.data.results[0].formatted_address;
            console.log(response.data.results[0].formatted_address);
          }
          this.spinner = false;
        })
        .catch((error) => {
          this.error = error.message;
          this.spinner = false;
          console.log(error.message);
        });
    },
    plotAllDrivers(latitude, longitude) {
      var loc = new this.google.maps.LatLng(latitude, longitude);
      var map = new this.google.maps.Map(this.$refs["map"], {
        zoom: 15,
        center: loc,
        mapTypeId: this.google.maps.MapTypeId.ROADMAP,
      });
      var icon = {
        url: require("@/assets/taxi.png"), // url
        scaledSize: new this.google.maps.Size(60, 60), // scaled size
        // origin: new this.google.maps.Point(0, 0), // origin
        // anchor: new this.google.maps.Point(0, 0), // anchor
      };
      this.traceDriversLocation();
      this.locs.forEach((lc) => {
        var marker = new this.google.maps.Marker({
          icon: icon,
          position: new this.google.maps.LatLng(lc.loc[0], lc.loc[1]),
          map: map,
          title: lc.driver.name,
        });
        var infowindow = new this.google.maps.InfoWindow({
          content: `<h3>${lc.driver.name}</h3><h4>${lc.driver.phone}</h4>`,
        });
        marker.addListener("click", function () {
          infowindow.open(map, marker);
        });
      });
    },
    showLocationOnTheMap(latitude, longitude) {
      // Show & center the Map based oon
      console.error(latitude, longitude);
      var map = new this.google.maps.Map(this.$refs["map"], {
        zoom: 15,
        center: new this.google.maps.LatLng(latitude, longitude),
        mapTypeId: this.google.maps.MapTypeId.ROADMAP,
      });
      new this.google.maps.Marker({
        position: new this.google.maps.LatLng(latitude, longitude),
        map: map,
      });
      this.plotAllDrivers(latitude, longitude);
    },
    showMessage(type, timeout, message) {
      var conf = {
        position: "top-right",
        timeout: timeout,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        draggablePercent: 0.6,
        showCloseButtonOnHover: false,
        hideProgressBar: true,
        closeButton: "button",
        icon: true,
      };
      switch (type) {
        case "error":
          this.$toast.error(message, conf);
          break;
        case "info":
          this.$toast.info(message, conf);
          break;
        case "success":
          this.$toast.success(message, conf);
          break;
        default:
          break;
      }
    },
  },
};
</script>


