<script>
import CoreAdminService from "../coreDataService";
export default {
  mounted() {
    this.getAllDrivers();
  },
  methods: {
    getAllDrivers() {
      this.loadingDrivers = true;
      CoreAdminService.driversData().on("value", (data) => {
        this.loadingDrivers = false;
        Object.keys(data.val()).forEach((x) => {
          this.driversData.push(data.val()[x]);
        });
      });
    },
  },
  data() {
    return {
      loadingDrivers: false,
      headers: [
        {
          text: "Name",
          align: "start",
          sortable: false,
          value: "name",
        },
        {
          text: "Email",
          align: "start",
          sortable: false,
          value: "email",
        },
        {
          text: "Phone No",
          align: "start",
          sortable: false,
          value: "phone",
        },
        {
          text: "New Ride",
          align: "start",
          sortable: false,
          value: "newRide",
        },
        {
          text: "Earnings",
          align: "start",
          sortable: false,
          value: "earnings",
        },
        {
          text: "Ratings",
          align: "start",
          sortable: false,
          value: "ratings",
        },
      ],
      driversData: [],
    };
  },
};
