import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Dashboard from '../views/Dashboard.vue'
import Drivers from '../views/Drivers.vue'
import RideRequests from '../views/RideRequests.vue'
import Customers from '../views/Customers.vue'
import login from '../views/login.vue'
import blogs from '../views/blogs.vue'
import notifications from '../views/notifications.vue'
import singledriver from '../views/singledriver.vue'
import singlerider from '../views/singlerider.vue'
import UserLocation from '../views/UserLocation.vue'




Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home
  },

  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard,
    children: [
      {
        path: "/maps",
        name: "maps",
        component: UserLocation
      },
      {
        path: "/drivers",
        name: "Drivers",
        component: Drivers
      },
      {
        path: '/blogs',
        name: 'blogs',
        component: blogs
      },
      {
        path: '/notifications',
        name: 'notifications',
        component: notifications
      },
      {
        path: '/singledriver',
        name: 'singledriver',
        component: singledriver
      },
      {
        path: '/singlerider',
        name: 'singlerider',
        component: singlerider
      },
      {
        path: "/customers",
        name: "Customers",
        component: Customers
      },
      {
        path: "/riderequests",
        name: "RideRequests",
        component: RideRequests
      },
    ]

  },
  {
    path: '/',
    name: 'login',
    component: login
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
