<script>
import CoreAdminService from "../coreDataService";
export default {
  mounted() {
    this.getAllDrivers();
  },
  methods: {
    getAllDrivers() {
      this.loadingData = true;
      CoreAdminService.getAll().on("value", (data) => {
        this.loadingData = false;
        Object.keys(data.val()).forEach((e) => {
          if (Object.keys(data.val()[e]).length > 1) {
            this.allUsers.push(data.val()[e]);
            console.warn(data.val()[e]);
          }
        });
        // if Object.keys(data.val()).length > 1:
        // Object.keys(data.val()).forEach((x) => {
        //   this.driversData.push(data.val()[x]);
        // });
      });
    },
  },
  data() {
    return {
      allUsers: [],
      loadingData: false,
      headers: [
        {
          text: "Name",
          align: "start",
          sortable: false,
          value: "name",
        },
        {
          text: "Email",
          align: "start",
          sortable: false,
          value: "email",
        },
        {
          text: "Phone No",
          align: "start",
          sortable: false,
          value: "phone",
        },
        {
          text: "",
          align: "start",
          sortable: false,
          value: "actions",
        },
      ],
    };
  },
};
</script>
